$font-xx-large  :   25px;
$font-x-large   :   21px;
$font-large     :   18px;
$font-medium    :   16px;
$font-small     :   13px;
$font-x-small   :   9px;

$border: 1px #e7e7e7 solid;

$white                :           #ffffff;
$wild_sand            :           #f4f4f4;
$black                :           #000000;
$alto                 :           #dddddd;
$gray                 :           #333333;
$dusty_gray           :           #999999;
$tint_gray            :           #ebebeb;
$dusty_gray_light     :           #979797;
$santas_gray          :           #a3a3a6;
$light_gray           :           #cacaca;
$turquoise_light      :           #70ffeb;
$turquoise            :           #21e0d8;
$green                :           #124f52;
$gold                 :           #fad400;
$violet               :           #d127fc;
$purple               :           #cf25f8;
$purple_dark          :           #cd36f5;
$purple_flirt         :           #990099;
$red                  :           #f82353;
$cardinal_red         :           #ce1d43;
$orange               :           #ff8e1f;
$sea-orange           :           #EF9955;
$dark_orange          :           #d46900;
$rose_pink            :           #e60382;
$rose_pink_dark       :           #ab1661;
$green                :           #21E078;
$lilac_pink           :           #D6A7CD;


$color_list: (
  purple              :           $purple,
  turquoise           :           $turquoise,
  violet              :           $violet,
  red                 :           $red,
  orange              :           $orange,
  rose_pink           :           $rose_pink,
  gold                :           $gold,
  lilac_pink          :           $lilac_pink
);

@mixin float-clear($value) {
  float: $value;
  clear: $value;
}

@mixin line-break-value($pos, $width, $color) {
  border-#{$pos}: $width solid $color;
}
